import React from 'react'
import Header from '../../components/Header/Header'
import Card from '../../components/ClassicCard/Card'
import TextDiv from '../../components/ClassicDiv/TextDiv'
import PricingCards from '../../components/PricingCards/PricingCards'
import LinkPage from '../../components/LinkPages/LinkPages'
import Footer from '../../components/Footer/Footer'
import './HomePage.scss'

export default function HomePage() {

    return (
        <div className="home-page">
            <div className="header">
                <Header/>
            </div>

            <div className="background-title-image">
                <h1>Airsoft API</h1>
            </div>

            <div className="card-main-container container">
                <div className="card-container">
                    <Card
                    // Normal state
                        imageUnactive="images/collaboration-unactive.svg"
                        title="Collaboration"
                    // On mouseOver
                        imageActive="images/collaboration-active.svg"
                        body="API collaborative dont les donnes sont implmentes par l'quipe comme par vous. Ce qui permet d'avoir la base de donnes la plus complte possible, et avec des mises  jour quotidiennes"
                    />

                    <div className="card-separator"></div>

                    <Card
                    // Normal state
                        imageUnactive="images/fiability-unactive.svg"
                        title="Fiabilit"
                        imageActive="images/fiability-active.svg"
                        body="Les donnes sont vrifies par une quipe de modration afin de confirmer la fiabilit des donnes. Ainsi, les utilisateurs disposent tout le temps d'une base de donnes dont ils peuvent avoir confiance"
                    />

                    <div className="card-separator"></div>

                    <Card
                    // Normal state
                        imageUnactive="images/simplicity-unactive.svg"
                        title="Simplicit"
                    // On mouseOver
                        imageActive="images/simplicity-active.svg"
                        body="Cette API est trs simple dutilisation ! Vous pouvez rcuperer les donnes avec de simples requettes."
                    />

                    <div className="card-separator"></div>

                    <Card
                    // Normal state
                        imageUnactive="images/complete-unactive.svg"
                        title="Complte"
                    // On mouseOver
                        imageActive="images/complete-active.svg"
                        body="Cette Api recense de nombreuses rpliques dairsoft en fournissant des informations utiles et prcises venant de sources vrifies."
                    />
                </div>
            </div>

            <TextDiv
                title="Informations complmentaires sur l'API"
                subtitle="Compltez votre site avec notre API"
                bodyTitle="Airsoft API permet de rcuprer simplement les informations concernant les rpliques d'airsoft"
                body1="Rcuprer aisment des donnes comme le modle, la catgorie, la marque ou la puissance dune rplique dAirsoft par exemple. Mais ce nest pas tout !
                Vous pourrez retrouver des informations complmentaires comme des photos, des descriptions et plein dautres informations."
                body2="Airsoft API cest des donnes sres et fiables, vrifies par les quipes, afin de vous transmettre les informations les plus prcises possibles."
                button="Documentation"
            />

            <div className="pricing-main-container container">
                <h2 className="pricing-title">
                    <div className="pricing-line"></div>
                    Tarifs et plans
                </h2>
                <div className="pricing-cards">
                    <PricingCards
                        className="pricing-card-container blue"
                        title="Programme de contribution"
                        subtitle="Contribuez pour profiter des avantages"
                        firstArgument="Bnficiez d'un accs gratuit  l'API"
                        secondArgument="Les contributions sont contrles par la modration"
                        description="C'est aussi un moyen de nous soutenir, alors n'hsitez plus ! "
                    />
                    <PricingCards
                        className="pricing-card-container white"
                        title="Abonnement"
                        subtitle="Abonnez vous pour profiter des avantages"
                        firstArgument="Accdez  l'API en illimit pour 7 par mois"
                        secondArgument="Payez 70 par an afin de profiter d'une rduction"
                        description="Accdez  notre service sans avoir besoin de contribuer"
                    />
                </div>
                <div className="pricing-button">
                    <LinkPage 
                        className="button-blue-big"
                        link="/pricing"
                        alt="link"
                        text="Les tarifs"
                    />
                </div>
            </div>

            <div className="footer">
                <Footer/>
            </div>
        </div>
    )
}
